import Vue from 'vue'
import Router from 'vue-router'

const  SheTuan = r=> require.ensure([],()=>r(require('@/components/shetuan/ShetuanDream.vue')))
const  copy = r=> require.ensure([],()=>r(require('@/components/shetuan/copy_demo.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'ShetuanDream',
      component: SheTuan
    },
    {
      path:'/copy',
      name:'copy_demo',
      component: copy
    }
  ]
})



